@import "https://fonts.googleapis.com/css2?family=Cormorant:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap";
html {
  color: #222;
  font-size: 1em;
  line-height: 1.4;
}

::selection {
  text-shadow: none;
  background: #b3d4fc;
}

hr {
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
  display: block;
}

audio, canvas, iframe, img, svg, video {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

html, body {
  height: 100%;
  margin: 0;
  font-family: Cormorant, Times New Roman, Times, Georgia, serif;
}

body {
  background: #fff fixed;
}

#main {
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  grid-template-rows: .3fr 2.4fr .3fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-flow: row;
  gap: 0;
  padding: 6em 12% 6%;
  display: grid;
}

.header {
  grid-area: 1 / 1 / 2 / 4;
  justify-content: space-between;
  align-items: baseline;
  display: flex;
}

.footer {
  grid-area: 3 / 1 / 4 / 4;
  justify-content: space-between;
  align-items: baseline;
  display: flex;
}

.content {
  min-width: 10em;
  width: calc(100% - 2em);
  grid-area: 2 / 1 / 3 / 4;
  place-self: flex-start center;
}

.content * {
  margin: initial;
}

#logo {
  height: 2em;
  color: #000;
  display: block;
}

#logo-hoverbox {
  z-index: 1;
  pointer-events: auto;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

#logo-text {
  pointer-events: none;
  color: #000;
  margin: 0;
  font-size: 2em;
}

.buttons {
  height: min-content;
  align-items: flex-end;
  gap: 1em;
  display: flex;
}

.buttons > a {
  color: #000;
  letter-spacing: -.05em;
  font-size: 1.4em;
  font-weight: 500;
  text-decoration: none;
}

.funbox {
  grid-area: 3 / 1 / 4 / 2;
}

.copyright {
  grid-area: 3 / 3 / 4 / 4;
  place-self: end;
}

.copyright > p {
  color: #ddd;
}

.copyright, .funbox {
  white-space: nowrap;
  font-size: .65em;
  font-style: italic;
}

.fade {
  opacity: 0;
  transition: opacity 2s;
}

.hidden, [hidden] {
  display: none !important;
}

.sr-only {
  clip: rect(0, 0, 0, 0);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.sr-only.focusable:active, .sr-only.focusable:focus {
  clip: auto;
  height: auto;
  white-space: inherit;
  width: auto;
  margin: 0;
  position: static;
  overflow: visible;
}

.invisible {
  visibility: hidden;
}

.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

@media print {
  *, :before, :after {
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
    background: #fff !important;
  }

  a, a:visited {
    text-decoration: underline;
  }

  a[href]:after {
    content: " (" attr(href) ")";
  }

  abbr[title]:after {
    content: " (" attr(title) ")";
  }

  a[href^="#"]:after, a[href^="javascript:"]:after {
    content: "";
  }

  pre {
    white-space: pre-wrap !important;
  }

  pre, blockquote {
    page-break-inside: avoid;
    border: 1px solid #999;
  }

  thead {
    display: table-header-group;
  }

  tr, img {
    page-break-inside: avoid;
  }

  p, h2, h3 {
    orphans: 3;
    widows: 3;
  }

  h2, h3 {
    page-break-after: avoid;
  }
}

